  }
  .home_slider_content h1:first-child {
    font-size: 92px;
  }
  .home_slider_content h1:nth-child(2) {
    font-size: 80px;
    margin-top: -24px;
  }
  .footer_blog_content {
    padding-left: 74px;
  }
