/************
6. 991px
************/

@media only screen and (max-width: 991px) {
  .main_nav_container {
    display: none;
  }
  .home_slider_content h1:first-child {
    font-size: 72px;
  }
  .home_slider_content h1:nth-child(2) {
    font-size: 60px;
    margin-top: -18px;
  }
  .home_slider_button {
    margin-top: 58px;
  }
  .hamburger {
    display: block;
  }
  .search {
    height: auto;
    padding-top: 100px;
    padding-bottom: 100px;
  }
  .search_tabs_container {
    position: relative;
    bottom: auto;
    left: auto;
    width: auto;
  }
  .search_tabs {
    height: auto;
  }
  .search_tab {
    height: 62px;
    width: 100%;
    padding-left: 15px;
  }
  .search_tab:first-child {
    border-top-left-radius: 0px;
    padding-left: 15px;
  }
  .search_tab:last-child {
    border-top-right-radius: 0px;
  }
  .search_panel {
    display: none !important;
    margin-top: 50px;
  }
  .search_panel.active {
    display: -ms-flexbox !important;
    display: flex !important;
  }
  .search_item {
    width: 100% !important;
    margin-bottom: 23px;
  }
  .search_item:last-child {
    margin-bottom: 0px;
  }
  .search_button {
    margin-top: 9px;
  }
  .intro_col {
    margin-bottom: 60px;
  }
  .intro_col:last-child {
    margin-bottom: 0px;
  }
  .intro_item {
    height: 810px;
  }
  .cta_item {
    padding-left: 30px;
    padding-right: 30px;
  }
  .cta_slider_nav {
    display: none !important;
  }
  .offers_image_container {
    height: 708px;
  }
  .offers_content {
    margin-top: 40px;
  }
  .test_slider_nav {
    display: none !important;
  }
  .footer_column {
    margin-bottom: 60px;
  }
  .footer_column:last-child {
    margin-bottom: 0px;
  }
  .copyright {
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .copyright_content {
    height: auto;
    margin-top: 15px;
  }
  .footer_nav_container {
    height: auto;
  }
  .footer_nav_item {
    display: block;
  }
}

