/************
11. 575px
************/

@media only screen and (max-width: 575px) {
  .button a {
    font-size: 13px;
  }
  p {
    font-size: 13px;
  }
  .top_bar {
    display: none;
  }
  .button {
    height: 44px;
    border-radius: 22px;
  }
  .button a {
    line-height: 44px;
    padding-left: 36px;
    padding-right: 36px;
  }
  .logo a img {
    width: 24px;
  }
  .logo a {
    font-size: 24px;
  }
  .home_slider_nav {
    display: none !important;
  }
  .home_slider_content {
    margin-top: -10%;
  }

  .home_slider_content h1:first-child {
    font-size: 48px;
  }
  .home_slider_content h1:nth-child(2) {
    font-size: 43px;
    margin-top: 0px;
  }
  .home_slider_button {
    margin-top: 30%;
  }
  .home_slider_dots {
    display: none !important;
  }
  .main_nav_col {
    height: 60px;
  }
  .header.scrolled .main_nav_col {
    height: 60px;
  }
  .logo_container {
    -webkit-transform: none;
    -ms-transform: none;
    transform: none;
  }
  .content_search {
    -webkit-transform: translateY(2px);
    -ms-transform: translateY(2px);
    transform: translateY(2px);
  }
  .header.scrolled .content_search {
    -webkit-transform: translateY(2px);
    -ms-transform: translateY(2px);
    transform: translateY(2px);
  }
  .hamburger {
    -webkit-transform: none;
    -ms-transform: none;
    transform: none;
  }
  .logo a {
    font-size: 18px;
  }
  .logo a img {
    width: 16px;
    margin-right: 7px;
  }
  .hamburger i {
    font-size: 18px;
  }
  .content_search svg {
    width: 15px;
    height: 15px;
  }
  .menu_close_container {
    top: 15px;
    right: 28px;
  }
  .menu_logo {
    margin-bottom: 17px;
  }
  .menu_logo a img {
    width: 28px !important;
  }
  .menu_item a {
    font-size: 24px;
  }
  .search_button {
    line-height: 44px;
  }
  .intro_item {
    height: calc((100vw - 30px) / 0.85);
  }
  .section_title {
    font-size: 21px;
  }
  .cta_item {
    padding-left: 15px;
    padding-right: 15px;
  }
  .cta_title {
    font-size: 24px;
  }
  .test_item {
    padding-top: 26px;
  }
  .test_icon {
    left: 20px;
    width: 52px;
    height: 52px;
  }
  .test_content {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 36px;
  }
  .test_item_info {
    left: 20px;
  }
  .test_name {
    font-size: 12px;
  }
  .test_date {
    font-size: 10px;
  }
  .test_quote_title {
    font-size: 16px;
    padding-left: 1px;
  }
  .test_quote_text {
    margin-top: 5px;
  }
  .trending_title a {
    font-size: 14px;
  }
  .trending_price {
    font-size: 12px;
  }
  .trending_location {
    font-size: 12px;
  }
  .offers_image_container {
    height: calc((100vw - 30px) / 0.974);
  }
  .trending_item {
    margin-bottom: 30px;
  }
  .contact_title {
    font-size: 24px;
  }
  .contact_form_name {
    width: 100%;
    margin-right: 0px;
  }
  .contact_form_email {
    width: 100%;
    margin-top: 11px;
  }
  .form_submit_button {
    line-height: 44px;
  }
  .footer_about_text {
    font-size: 13px;
  }
}

