************/

@media only screen and (max-width: 991px) {
  .main_nav_container {
    display: none;
  }
  .hamburger {
    display: block;
  }
  .intro {
    padding-top: 71px;
  }
  .intro_image {
    position: relative;
    right: auto;
    top: auto;
  }
  .intro_content {
    margin-top: 30px;
  }
  .add_content {
    padding-left: 71px;
  }
  .milestone_col {
    margin-bottom: 60px;
  }
  .milestone_col:last-child {
    margin-bottom: 0px;
  }
  .footer_column {
    margin-bottom: 60px;
  }
  .footer_column:last-child {
    margin-bottom: 0px;
  }
  .copyright {
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .copyright_content {
    height: auto;
    margin-top: 15px;
  }
  .footer_nav_container {
    height: auto;
  }
  .footer_nav_item {
    display: block;
  }
}

