9. 768px
************/

@media only screen and (max-width: 768px) {
  .home_title {
    font-size: 48px;
  }
  .stats_years {
    padding-left: 107px;
  }
  .stats_years_last {
    display: none !important;
  }
  .stats_years_new {
    float: left !important;
  }
  .stats_bar {
    width: 100%;
  }
  .stats_new {
    padding-right: 0px;
    padding-left: 40px;
    width: 40px;
    -webkit-transform: translateY(14px);
    -ms-transform: translateY(14px);
    transform: translateY(15px);
  }
  .stats_new_icon {
    right: auto;
    left: 0;
    width: 40px;
  }
  .stats_new_content {
    margin-left: 10px;
  }
  .stats_last {
    display: none;
  }
}

