11. 575px
************/

@media only screen and (max-width: 575px) {
  p {
    font-size: 13px;
  }
  h1 {
    font-size: 28px;
  }
  .top_bar {
    display: none;
  }
  .button {
    height: 44px;
    border-radius: 22px;
  }
  .button a {
    line-height: 44px;
    padding-left: 36px;
    padding-right: 36px;
  }
  .logo a img {
    width: 24px;
  }
  .logo a {
    font-size: 24px;
  }
  .main_nav_col {
    height: 60px;
  }
  .header.scrolled .main_nav_col {
    height: 60px;
  }
  .logo_container {
    -webkit-transform: none;
    -ms-transform: none;
    transform: none;
  }
  .content_search {
    -webkit-transform: translateY(2px);
    -ms-transform: translateY(2px);
    transform: translateY(2px);
  }
  .header.scrolled .content_search {
    -webkit-transform: translateY(2px);
    -ms-transform: translateY(2px);
    transform: translateY(2px);
  }
  .hamburger {
    -webkit-transform: none;
    -ms-transform: none;
    transform: none;
  }
  .logo a {
    font-size: 16px;
  }
  .logo a img {
    width: 16px;
    margin-right: 7px;
  }
  .hamburger i {
    font-size: 18px;
  }
  .content_search svg {
    width: 15px;
    height: 15px;
  }
  .menu_close_container {
    top: 15px;
    right: 28px;
  }
  .menu_logo {
    margin-bottom: 17px;
  }
  .menu_logo a img {
    width: 28px !important;
  }
  .menu_item a {
    font-size: 24px;
  }
  .section_title {
    font-size: 21px;
  }
  .home {
    width: 100%;
    height: 375px;
    background: transparent;
  }
  .home_title {
    font-size: 24px;
  }
  .intro_title {
    font-size: 21px;
  }
  .intro_text {
    margin-top: 45px;
  }
  .stats_text {
    padding-left: 0px;
    padding-right: 0px;
  }
  .stats_years {
    float: none !important;
    padding-left: 0px;
  }
  .stats_contents {
    padding-left: 0px;
    padding-right: 0px;
  }
  .stats_number {
    font-size: 18px;
  }
  .stats_type {
    font-size: 12px;
  }
  .add_content {
    padding-left: 40px;
  }
  .footer_about_text {
    font-size: 13px;
  }
}

